import React from 'react';
import { Input } from './Model';
import { shouldNotHaveBill, tipMultiplier } from './TipEngine';

function toDollarString(totalInCent: number): string {
  let centAmount = ("0" + (totalInCent % 100).toString()).slice(-2)
  let dollarAmount = Math.floor(totalInCent / 100).toString()
  return `${dollarAmount}.${centAmount}`
}

interface ResultComponentProps {
  tipAmount: number,
  billAmount: number,
  input: Input,
};

export class ResultComponent extends React.Component<ResultComponentProps, {}> {
  // eslint-disable-next-line
  constructor(props: ResultComponentProps) {
    super(props);
  }

  tipPercentage() {
    let x = tipMultiplier(this.props.input)
    if (x === null || x === undefined) {
      return null
    }
    return (x * 100).toFixed(0)
  }

  render() {
    let scenario = this.props.input.scenario
    if (scenario === null) {
      // eslint-disable-next-line
      throw "Assertion failure"
    }

    if (this.props.billAmount === 0 &&
      shouldNotHaveBill(scenario)) {
      return (
        <div>
          <div className='input-prompt'>
            <h2>Result</h2>
          </div>
          <div className='output-results'>
            <div className='output-result'>
              <table>
                <tr>
                  <td className='left-text'>Tip (fixed):</td>
                  <td>$</td>
                  <td className='right-text'>{toDollarString(this.props.tipAmount)}</td>
                </tr>
              </table>
            </div>
          </div>
        </div>
      )
    } else {
      let totalAmount = this.props.tipAmount + this.props.billAmount
      return (
        <div>
          <div className='input-prompt'>
            <h2>Result</h2>
          </div>
          <div className='output-results'>
            <div className='output-result'>
              <table>
                <tbody>
                  <tr>
                    <td className='left-text'>Bill:</td>
                    <td>$</td>
                    <td className='right-text'>{toDollarString(this.props.billAmount)}</td>
                  </tr>
                  <tr>
                    <td className='left-text'>Tip @ {this.tipPercentage()}%:</td>
                    <td>$</td>
                    <td className='right-text'>{toDollarString(this.props.tipAmount)}</td>
                  </tr>
                  <tr>
                    <td className='left-text'>Total:</td>
                    <td>$</td>
                    <td className='right-text'>{toDollarString(totalAmount)}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      )
    }
  }
};
